import type { ValidatedEventAPIGatewayProxyEvent } from '@libs/apiGateway';
import { formatJSONResponse } from '@libs/apiGateway';
import { middyfy } from '@libs/lambda';
import * as AWS from "aws-sdk";
import schema from './schema';
import * as bcrypt from 'bcryptjs';
import * as jwt from "jsonwebtoken";



const login: ValidatedEventAPIGatewayProxyEvent<typeof schema> = async (event) => {

  const dynamo = new AWS.DynamoDB.DocumentClient({
    region: 'lccalhost',
    endpoint: "http://localhost:8000",
  });

  try {

    if (!event.body.email || !event.body.password) {
      return formatJSONResponse({
        message: 'please provide email and password',

      })
    }

    let user = await dynamo.get({
      TableName: "users",
      Key: { email: event.body.email }
    }).promise();
    if (!user.Item) {
      return formatJSONResponse({
        message: `no user with this email`,
        statuscode: 404

      });
    }
    // checking password 
    const validatePassword = await bcrypt.compare(event.body.password, user.Item.password)
    if (!validatePassword) {
      return formatJSONResponse({
        message: `either  email or password is wrong`,
        statuscode: 404

      });

    }
    // generate token 
    let createToken = {
      email: user.Item.email
    };
    const token = jwt.sign(
      JSON.parse(JSON.stringify(createToken)),
      "SECRIT_KEY",
      {
        expiresIn: '3h',
      }
    );
    const { password, ...others }  = user.Item;
    return formatJSONResponse({

      user: others,
      token: token
      // event
    });
  } catch (error) {
    return formatJSONResponse({
      // message: `some error occured`,
      error: error

    });
  }

}

export const main = middyfy(login);
